import React, { Component } from "react";

class ProjectDescription extends Component {
	render() {
		return (
			<div className="video-description-wrapper">
				<div className="video-description">{this.props.content}</div>
			</div>
		)
	}
}

class PicProject extends Component {
	renderImage() {
		if (this.props.src != null)
			return (
				<img src={this.props.src} alt=""/>
			)
		else
			return (
				<div>placeholder</div>
			)
	}
	render() {
		return (
			<span className="section">
				<div className="video-wrapper">{this.renderImage()}</div>
				<ProjectDescription content={this.props.content}/>
			</span>
		)
	}
}

class VideoProject extends Component {
	renderImage() {
		if (this.props.src != null)
			return (
				<video controls muted autoPlay loop>
					<source src={this.props.src} type="video/mp4"/>
					Video couldn't load.
				</video>
			)
		else
			return (
				<div>placeholder</div>
			)
	}
	render() {
		return (
			<span className="section">
				<div className="video-wrapper">{this.renderImage()}</div>
				<ProjectDescription content={this.props.content}/>
			</span>
		)
	}
}

function handleErrors(response) {
	if (!response.ok)
		throw Error(response.status)
	return (response)
}

class Projects extends Component {
	renderProjectsHeader() {
		return (
			<div key="header">
				Projects at 42:
			</div>
		)
	}
	renderProject(project, index) {
		return (
			<div key={index} className="project-desc">
				{project.project.name + ' (score: ' + project.final_mark + ')'}<br/>
			</div>
		)
	}
	projectsState(parsed) {
		var projects = parsed.filter((project) => {
			const is_not_piscine = /^(?!.*(Day|[Ee]xam|Match|Eval)).*[a-zA-Z]+/
			return (is_not_piscine.test(project.project.name)
				&& (project.status === "finished" || project.status === "in_progress")
				&& project['validated?'])
		}).map((project, index) => (this.renderProject(project, index)))
		.sort((a, b) => (a.id - b.id))
		this.setState({'projects': [this.renderProjectsHeader(), ...projects]})
	}
	projectsError(error) {
		console.error(error)
		this.setState({projects: "Error fetching 42 projects"})
	}
	constructor(props) {
		super(props)
		this.state = {projects: "Loading 42 projects"}
		fetch('https://1xudvdicd9.execute-api.us-east-1.amazonaws.com/dev/projects/40802')
			.then(handleErrors)
			.then((response) => response.json())
			.then((parsed) => JSON.parse(parsed.body))
			.then(this.projectsState.bind(this))
			.catch(this.projectsError.bind(this))
	}
	renderProjects() {
		let descriptions = [
			<div>
				<a href="https://github.com/tacchinotacchi/lem-in">Lem-in</a> is a solver for a routing problem, where a number of resources ("ants") have to be routed from a start node
				to an end node, in an undirected graph where the nodes have capacity of 1 resource, with the smallest amount of instructions
				("turns") possible.
			</div>,
			<div>
				In the game of <a href="https://github.com/tacchinotacchi/filler_evolver">Filler</a>, two "champions" use random tetris-like pieces supplied randomly
				to try and conquer as much of the field as possible. I thought it would be a nice addition to write some fuzzy parameters into champion's code and write a genetic algorithm to evolve them.
			</div>,
			<div>
				placeholder
			</div>,
			<div>Corewars coming soon!</div>
		]
		let projects = [
			<VideoProject key={1} src={require("./resources/lemin.mp4")} content={descriptions[0]}/>,
			<VideoProject key={2} src={require("./resources/filler.mp4")} content={descriptions[1]}/>,
			<PicProject key={3} src={require("./resources/42_Logo.svg")} content={this.state.projects}/>,
			<PicProject key={4} src={null} content={descriptions[3]}/>
		]
		return projects
	}
	render() {
		return (
			<div className="projects">
				<h1>Projects</h1>
				{this.renderProjects()}
	 		 </div>
		)
	}

}

export default Projects;
